<template>
    <div>
        <div class="pannel-widget" style="margin-bottom: 1rem">
            <h2 class="base-title-widget"></h2>
            <hr class="seg-widget"/>

            <!---->
            <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

        </div>
        <div class="pannel-widget" style="padding-bottom: 3rem">
            <h2 class="base-title-widget bt-w-blue" style="margin-bottom: 2rem"></h2>
                <div style="padding: 0 2rem">
                    <div class="pcenter">
                        <img class="p-w-round-img" :src="user_info.cover" @error="reset_cover" />
                    </div>
                    <div class="list-text-widget">
                        <div class="lt-w-item">
                            <span></span>
                            <input type="text" class="lt-w-item-after" readonly v-model="user_info.uname">
                        </div>
                        <div class="lt-w-item">
                            <span></span>
                            <input type="text" class="lt-w-item-after" readonly v-model="user_info.sex">
                        </div>
                        <div class="lt-w-item">
                            <span></span>
                            <input type="text" class="lt-w-item-after" readonly v-model="user_info.age">
                        </div>
                        <div class="lt-w-item">
                            <span></span>
                            <input type="text" class="lt-w-item-after" readonly v-model="user_info.tel">
                        </div>
                        <div class="lt-w-item">
                            <span></span>
                            <input type="text" class="lt-w-item-after" readonly v-model="user_info.email">
                        </div>
                        <div class="lt-w-item">
                            <span></span>
                            <input type="text" class="lt-w-item-after" readonly v-model="user_info.addr">
                        </div>
                        <div class="lt-w-item">
                            <span></span>
                            <input type="text" class="lt-w-item-after" readonly v-model="user_info.jobs">
                        </div>
                    </div>
                </div>
            <hr class="seg-widget"/>
            <div class="pad-h-2"></div>

            <h2 class="base-title-widget bt-w-blue"></h2>
            <div style="padding: 0 1rem" v-if="user_info.edu.length > 0">
                <div class="edit-widget" v-for="edu of user_info.edu">
                    <div class="list-text-widget">
                        <div class="lt-w-item">
                            <span></span>
                            <input type="text" class="lt-w-item-after" readonly v-model="edu.start_time" placeholder="YYYY-MM-DD">
                        </div>
                        <div class="lt-w-item">
                            <span></span>
                            <input type="text" class="lt-w-item-after" readonly v-model="edu.end_time" placeholder="YYYY-MM-DD">
                        </div>
                        <div class="lt-w-item">
                            <span>/</span>
                            <input type="text" class="lt-w-item-after" readonly v-model="edu.chool">
                        </div>
                        <div class="lt-w-item">
                            <span></span>
                            <input type="text" class="lt-w-item-after" readonly v-model="edu.major">
                        </div>
                        <div class="lt-w-item">
                            <span></span>
                            <input type="text" class="lt-w-item-after" readonly v-model="edu.diploma">
                        </div>
                        <div class="lt-w-item">
                            <span></span>
                            <textarea class="lt-w-item-textarea" readonly v-model="edu.desc"></textarea>
                        </div>
                    </div>
                </div>
            </div>
            <div class="no-result-widget" v-else>
                <span class="icon-nodata nr-w-ico"></span>
                <span class="nr-w-text"></span>
            </div>
            <hr class="seg-widget"/>

            <div class="pad-h-2"></div>
            <h2 class="base-title-widget bt-w-blue"></h2>
            <div style="padding: 0 1rem" v-if="user_info.skill.length > 0">
                <div class="edit-widget" v-for="skill of user_info.skill">
                    <div class="list-text-widget">
                        <div class="lt-w-item">
                            <span></span>
                            <input type="text" class="lt-w-item-after" readonly v-model="skill.name">
                        </div>
                        <div class="lt-w-item">
                            <span></span>
                            <span class="lt-w-item-after">
                                <select v-model="skill.degree" class="lt-w-item-after-select">
                                    <option value="know"></option>
                                    <option value="shuxi"></option>
                                    <option value="skilled"></option>
                                    <option value="master"></option>
                                </select>
                            </span>
                        </div>
                        <div class="lt-w-item">
                            <span></span>
                            <textarea class="lt-w-item-textarea" readonly v-model="skill.desc"></textarea>
                        </div>
                    </div>
                </div>
            </div>
            <div class="no-result-widget" v-else>
                <span class="icon-nodata nr-w-ico"></span>
                <span class="nr-w-text"></span>
            </div>
            <hr class="seg-widget"/>

            <div class="pad-h-2"></div>
            <h2 class="base-title-widget bt-w-blue"></h2>
            <div style="padding: 0 1rem" v-if="user_info.pro.length > 0">
                <div class="edit-widget" v-for="pro of user_info.pro">
                    <div class="list-text-widget">
                        <div class="lt-w-item">
                            <span></span>
                            <input type="text" class="lt-w-item-after" readonly v-model="pro.name">
                        </div>
                        <div class="lt-w-item">
                            <span></span>
                            <input type="text" class="lt-w-item-after" readonly v-model="pro.date_time" placeholder="YYYY-MM-DD">
                        </div>
                        <div class="lt-w-item">
                            <span></span>
                            <a class="lt-w-item-after" :href="pro.link" v-if="pro.link">{{pro.link}} ()</a>
                            <span class="lt-w-item-after" v-else>{{pro.link}}</span>
                        </div>
                        <div class="lt-w-item">
                            <span></span>
                            <textarea class="lt-w-item-textarea" readonly v-model="pro.desc"></textarea>
                        </div>
                    </div>
                </div>
            </div>
            <div class="no-result-widget" v-else>
                <span class="icon-nodata nr-w-ico"></span>
                <span class="nr-w-text"></span>
            </div>
            <hr class="seg-widget"/>

            <div class="pad-h-2"></div>
            <h2 class="base-title-widget bt-w-blue"></h2>
            <div style="padding: 0 2rem">
                <div class="list-text-widget">
                    <div class="lt-w-item">
                        <span></span>
                        <textarea class="lt-w-item-textarea" v-model="user_info.desc"></textarea>
                    </div>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";

    export default {
        name: "user_home_resume",
        data() {
            return {

                /*aside_nav_widget*/
                aside_nav_data: [
                    {
                        name: "",
                        type: "route",
                        link: "/mob/user_home",
                    },
                    {
                        name: "",
                        type: "route",
                        link: "/mob/user_home_center",
                    },
                    {
                        name: "",
                        type: "route",
                        act: "act",
                        link: "/mob/user_home_resume",
                    },
                ],

                /**/
                user_info: {
                    uid: "",
                    cover: "",
                    uname: "",
                    sex: "",
                    age: "",
                    tel: "",
                    email: "",
                    addr: "",
                    jobs: "",
                    desc: "",
                    edu: [],
                    skill: [],
                    pro: []
                },

                /**/
                edu: {
                    start_time: "",
                    end_time: "",
                    chool: "",
                    major: "",
                    diploma: "",
                    desc: "",
                    del: []
                },

                /**/
                skill: {
                    name: "",
                    degree: "",
                    desc: "",
                    del: []
                },

                /**/
                pro: {
                    name: "",
                    link: "",
                    date_time: "",
                    desc: "",
                    del: []
                },

                /**/
                confirm: null,

            }
        },
        methods: {

            /**/
            reset_cover(e){
                e.currentTarget.src = "data:image/jpeg;base64,";
            },

            /**/
            record_time_format(time){
                return time + "T01:00";
            },

            /**/
            show_time_format(time){
                return time.split("T")[0];
            }

        },
        components: {
            "aside-nav-widget": AsideNav,
        },
        mounted() {
            this.confirm = this.$B.widgets.Confirm;
            this.confirm.load("");
            this.$axios.all([
                this.$axios.get("skillvi/"),
                this.$axios.get("education/"),
                this.$axios.get("project/"),
                this.$axios.get("userinfo/"),
            ]).then(this.$axios.spread((r1, r2, r3, r4) => {

                /**/
                let skill_data = r1.data;
                if (skill_data.length > 0) {
                    this.user_info.skill = this.$B.data_convert(skill_data, {
                        id: "1",
                        name: "skill_name",
                        degree: "skill_level",
                        desc: "skill_introduce"
                    })
                }

                /**/
                let edu_data = r2.data;
                if (edu_data.length > 0) {
                    this.user_info.edu = this.$B.data_convert(edu_data, {
                        id: "1",
                        start_time: (d)=>{
                            return this.show_time_format(d.Study_time)
                        },
                        end_time: (d)=>{
                            return this.show_time_format(d.end_time)
                        },
                        chool: "school_name",
                        major: "1",
                        diploma: "education",
                        desc: "honor"
                    })
                }

                /**/
                let pro_data = r3.data;
                if (pro_data.length > 0) {
                    this.user_info.pro = this.$B.data_convert(pro_data, {
                        id: "1",
                        name: "project_name",
                        link: "technology",
                        date_time: (d)=>{
                            return this.show_time_format(d.time_slot)
                        },
                        desc: "project_function"
                    })
                }

                /**/
                let user_data = r4.data[0];
                this.user_info.uid = user_data.id;
                this.user_info.cover = user_data.img;
                this.user_info.uname = user_data.name;
                this.user_info.sex = user_data.gender;
                this.user_info.age = user_data.age;
                this.user_info.tel = user_data.mobile;
                this.user_info.email = user_data.email;
                this.user_info.addr = user_data.address;
                this.user_info.jobs = user_data.expected_work;
                this.user_info.desc = user_data.assessment;

                this.$nextTick(function(){
                    window.document.documentElement.scrollTop = 60;
                    this.confirm.load("", "end");
                })
            }));
        }
    }
</script>

<style scoped>
    @import "../../../assets/css/pannel-widget.css";
    @import "../../../assets/css/seg-widget.css";
    @import "../../../assets/css/base-title-widget.css";
    @import "../../../assets/css/base-btn-widget.css";
    @import "../../../assets/css/list-text-widget.css";
    @import "../../../assets/css/edit-widget.css";
    @import "../../../assets/css/no-result-widget.css";
</style>


